'use client'

import { useState, useRef } from 'react'
import {
  Upload,
  FileText,
  Loader2,
  Sparkles,
  BookOpen,
  Search,
  CheckCircle,
  AlertCircle
} from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Textarea } from '@/components/ui/textarea'
import { Badge } from '@/components/ui/badge'
import { ScrollArea } from '@/components/ui/scroll-area'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { toast } from '@/hooks/use-toast'

interface Document {
  id: string
  name: string
  size: number
  status: 'uploading' | 'processing' | 'ready' | 'error'
}

interface Answer {
  text: string
  source?: string
}

export default function DocumentQAPage() {
  const [documents, setDocuments] = useState<Document[]>([])
  const [question, setQuestion] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [answer, setAnswer] = useState<Answer | null>(null)
  const fileInputRef = useRef<HTMLInputElement>(null)

  const handleFileUpload = async (files: FileList | null) => {
    if (!files) return

    const file = files[0]
    const doc: Document = {
      id: Date.now().toString(),
      name: file.name,
      size: file.size,
      status: 'uploading'
    }

    setDocuments([doc])

    const formData = new FormData()
    formData.append('file', file)

    try {
      setDocuments([{ ...doc, status: 'processing' }])

      const res = await fetch('/api/documents/upload', {
        method: 'POST',
        body: formData
      })

      if (!res.ok) throw new Error()

      setDocuments([{ ...doc, status: 'ready' }])
      toast({
        title: 'Success',
        description: 'Document uploaded successfully'
      })
    } catch {
      setDocuments([{ ...doc, status: 'error' }])
      toast({
        title: 'Error',
        description: 'Upload failed',
        variant: 'destructive'
      })
    }
  }

  const handleAskQuestion = async () => {
    if (!question.trim()) return

    setIsLoading(true)
    setAnswer(null)

    try {
      const res = await fetch('/api/answer', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ question })
      })

      const data = await res.json()
      setAnswer(data)
    } catch {
      toast({
        title: 'Error',
        description: 'Failed to get answer',
        variant: 'destructive'
      })
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-slate-50">
      <header className="border-b bg-white">
        <div className="container mx-auto px-4 py-4 flex items-center gap-3">
          <BookOpen className="h-6 w-6 text-primary" />
          <h1 className="text-2xl font-bold">Context Flow</h1>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8 grid lg:grid-cols-3 gap-6">
        {/* Upload */}
        <Card>
          <CardHeader>
            <CardTitle>Upload Document</CardTitle>
            <CardDescription>Upload OS notes or PDFs</CardDescription>
          </CardHeader>
          <CardContent>
            <div
              onClick={() => fileInputRef.current?.click()}
              className="border-2 border-dashed rounded-lg p-6 text-center cursor-pointer"
            >
              <FileText className="mx-auto mb-2" />
              <p>Click to upload</p>
              <input
                ref={fileInputRef}
                type="file"
                accept=".pdf,.txt"
                onChange={(e) => handleFileUpload(e.target.files)}
                className="hidden"
              />
            </div>

            {documents.map((doc) => (
              <div key={doc.id} className="mt-4 flex items-center gap-2">
                {doc.status === 'ready' && <CheckCircle className="text-green-500" />}
                {doc.status === 'processing' && <Loader2 className="animate-spin" />}
                {doc.status === 'error' && <AlertCircle className="text-red-500" />}
                <span>{doc.name}</span>
                <Badge>{doc.status}</Badge>
              </div>
            ))}
          </CardContent>
        </Card>

        {/* QA */}
        <Card className="lg:col-span-2">
          <CardHeader>
            <CardTitle>Ask a Question</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <Textarea
              value={question}
              onChange={(e) => setQuestion(e.target.value)}
              placeholder="Ask an OS question..."
              rows={4}
            />

            <Button onClick={handleAskQuestion} disabled={isLoading}>
              {isLoading ? (
                <>
                  <Loader2 className="mr-2 animate-spin" />
                  Processing
                </>
              ) : (
                <>
                  <Sparkles className="mr-2" />
                  Get Answer
                </>
              )}
            </Button>

            {answer && (
              <Alert className="mt-4">
                <AlertDescription className="whitespace-pre-wrap">
                  {answer.text}
                </AlertDescription>
              </Alert>
            )}
          </CardContent>
        </Card>
      </main>
    </div>
  )
}
